.btn{color:#27da5c;border-radius:99px;border:1px solid #27da5c;text-decoration:none;text-transform:uppercase;padding:.5rem 1.5rem;font-weight:700;outline:none;background-color:#fff;cursor:pointer;margin-right:1rem;-webkit-transition:all .22s linear;transition:all .22s linear}.btn.primary{background-color:#27da5c;color:#fff}.btn:active{-webkit-box-shadow:inset 1px 1px 1px rgba(0,0,0,0.3);box-shadow:inset 1px 1px 1px rgba(0,0,0,0.3)}@media (any-hover: hover){.btn:hover{opacity:.8}}
